.logobar{
  position: relative;
  top: $height-header-xs;

  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 1rem;

  .logobar--logo {
    img {
      height: 50px;
      padding: 1rem;
    }
  }
}


@media screen and (min-width: 512px) {
  .logobar {
    flex-direction: row;
    justify-content: space-around;
  }
}

@media screen and (min-width: 768px) {
  .logobar{
    justify-content: flex-end;

    .logobar--logo {
      margin-left: 4rem;
    }
  }
}
